<template>
    <div class='Home'>

        <section class="blog-body">
            <p v-if="flag===0" class="nothing">Nothing</p>
            <div v-else class="blog-list">
                <ul class="list-article">
                    <li class="article" v-for="(blog,index) in showingBlogs" :key="blog.article_id">
                        <div class="article-header">
                            <h2 class="article-title">
                                <!-- <router-link to="{path:'/contents',query:{id:article.id}}" class="title-link">{{article.title}}</router-link> -->
                                <span class="title-link" @click="tocontent(blog)">{{blog.title}}</span>
                                <el-tag
                                        size="mini"
                                        hit
                                        v-for="tag in tags[index]"
                                        :key="tag"
                                        style="margin-right:10px"
                                >{{tag}}</el-tag>
                            </h2>
                            <p class="article-body">{{blog.summary}}</p>
                        </div>
                        <div class="article-footer">
              <span class="iconfont icon-riqi icons">
                <span class="content">{{formatDateTime(myTime(blog.post_time))}}</span>
              </span>

              <span class="iconfont icon-chakantiezihuifu icons">
                <span class="content">{{blog.comment_count}}评论</span>
              </span>

              <span class="iconfont icon-yanjing icons">
                <span class="content">{{blog.view}}阅读量</span>
              </span>
                        </div>
                    </li>
                </ul>
            </div>
            <el-pagination
                    small
                    @size-chage="handleSizeChange"
                    @current-change="handleCurrentChange"
                    layout="prev, pager, next"
                    :total="count"
                    :page-size="pageSize"
                    style="text-align: center;margin: 15px auto;"
            ></el-pagination>
        </section>


    </div>
</template>

<script>
    import {mapState, mapMutations} from 'vuex'
    // import eventBus from '@/api/eventBus'

    export default {
        name: "Home",


        data() {
            return {
                showingBlogs: [],
                // Blogs: [],
                drafts: [],
                blogs: [],
                tags: [],
                flag: 0,
                pageNum: 1,
                pageSize: 5,
                count: 0
            };
        },
        beforeRouteLeave(to, from, next){
          if (to.path==='/searchBlog') {
              // this.blogs.splice(0, this.blogs.length)
              // console.log(this.blogs);
              this.blogs = this.filterBlogs
              // this.filterBlogs.forEach(item => {
              //     this.blogs.push(item)
              // })
              // console.log(this.blogs);

              this.pageNum = 1
              this.changeData()
              next()
          } else if (to.path==='/home') {
              this.blogs = this.$store.state.allBlogs
              this.pageNum = 1
              this.changeData()
              next()
          } else {
              next()
          }
        },

        watch: {
        },
        created(){
            // console.log('home');
            this.$axios.get('/api/blog/getBlog').then((response) => {
                    let res = response.data
                    if (res.status) {
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        })
                    } else {
                        this.blogs = [...res.result]
                        // console.log(this.blogs);
                        this.changeData()
                        // if (this.$route.path==='/home') {
                        //     this.changeBlogs(this.blogs)
                        console.log(this.allBlogs);
                        // }
                        this.flag = 1
                    }

                }),


            this.$axios.get('/api/blog/getDraft').then((response) => {
                    let res = response.data
                    if (res.status) {
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        })
                    } else {
                        this.drafts = [...res.result]
                        console.log(this.drafts);
                        // if (this.$route.path==='/home') {
                        //     this.changeBlogs(this.blogs)
                        // }
                    }

                })
            // eventBus.$on('init',(payload) => {
            //     // this.blogs = payload
            //     console.log(payload);
            // })

        },
        watch: {
            blogs(){
                this.count = this.blogs.length
                // this.changeData()
            }
        },
        computed: {
          ...mapState(['allBlogs', 'filterBlogs']),
            allBlogs: {
                get: function () {
                    return this.$store.state.allBlogs;
                },
                set: function (val) {
                    this.$store.state.allBlogs = val
                }
            }
        },
        methods: {
            ...mapMutations(['changeBlogs']),
            changeData(x=0, y=5){
                this.count = this.blogs.length
                if (this.count>5) {
                    this.showingBlogs = this.blogs.slice(x, y)
                } else {
                    this.showingBlogs.splice(0,this.showingBlogs.length)
                    this.showingBlogs = this.blogs
                }

            },
            tocontent(blog) {
                this.$router.push({ path: "/content", query: {article_id: blog.article_id}  });
            },
            handleCurrentChange(val) {
                this.changeData((val-1)*5, val*5)

            },
            handleSizeChange(val) {

            },

        }
    }

</script>

<style scoped>
    @import '~assets/css/font.css';
    .icons {
        font-size: 14px;
        color: #909399;
        margin-right: 10px;
    }
    .content {
        margin-left: 3px;
    }
    .blog-body {
        max-width: 1200px;
        margin: 0px auto 0;
        position: relative;
        padding-left: 15px;
        padding-right: 15px;
    }
    .blog-list ul {
        list-style: none;
    }
    .title-link {
        vertical-align: middle;
        cursor: pointer;
    }
    .title-link:hover {
        color: #409eff;
    }
    .artic a {
        color: #24292e;
    }

    .article {
        margin: 0 0 7px;

        background-color: #eeeeee;
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
        border-radius: 0;
    }
    .article-header {
        padding: 10px 20px 0;
        /* border-bottom: 1px solid #f2f6fc; */
    }
    .article-title{
        /*border-radius: 10px;*/
    }
    .article-footer {
        padding: 8px 20px;
        border-top: 1px solid #f3f3f3;
        line-height: 27px;
        overflow: hidden;
        font-size: 12px;
    }
    .blog-list .article .article-title {
        font-size: 20px;
        font-weight: 400;
    }
    .blog-list .article .article-body {
        margin: 10px 0;
        color: #666;
        word-break: break-all;
    }
    .Home{
        height: 600px;
    }
</style>