import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

export default new Vuex.Store({


    state: {
        allBlogs: [],
        allDrafts: [],
        filterBlogs: [],
        // length: 0,
    },
    mutations: {
        changeBlogs(state, payload){
            state.allBlogs = payload
        },
        changeDrafts(state, payload){
            state.allDrafts = payload
        },
        changeFilterBlogs(state, payload){
            state.filterBlogs = payload
        },
        clear(state){
            state.filterBlogs.splice(0,state.filterBlogs.length)
        }

    }

});
