<template>
    <div class='SideBarItem' @click='handleClick'>
        <slot></slot>
    </div>
</template>

<script>
    export default {
        name: "SideBarItem",
        // data(){
        //     return{
        //         to:''
        //     }
        // },
        props: {
            to:{

            }
        },
        methods: {
            handleClick(){
                if(this.$route.path!==this.to){
                    this.$router.replace(this.to)
                }
            }
        }
    }
</script>

