<template>
  <div class='app'>
      <el-container>
        <el-aside width="250px">
            <side></side>
        </el-aside>
        <el-container>
          <el-header height='85px'>
            <common-nav-bar></common-nav-bar>
          </el-header>
          <el-main>
              <transition-box :name='name'>
                      <router-view class='center'></router-view>
              </transition-box>
          </el-main>
        </el-container>
      </el-container>
  </div>
</template>

<script>
  import Home from 'views/home/Home'
  import Side from 'components/content/side/Side'
  import AdminSide from "./views/admin/base/AdminSide";
  export default {
    components: {
      AdminSide,
      Home,
      Side,
    },
      data(){
        return {
            name: '',
        }
      },
      watch: {
          $route(to,from){
              if (from.path==='/'){
                  return
              }
              if (from.meta.index<to.meta.index){
                  this.name='left'
              }
              else{
                  this.name='right'
              }
          }
      }
  }
</script>
<style lang=scss scoped>
    @import "assets/css/reset.css";
    html,body,#app,.el-container{
    /*设置内部填充为0，几个布局元素之间没有间距*/
    padding: 0px;
    /*外部间距也是如此设置*/
    margin: 0px;
    /*统一设置高度为100%*/
    height: 100%;
  }
    .center {
        /*position: absolute;*/
        width: 100%;
        /*height: calc(100% - 49px)*/
    }
    .app{
        height:100%;
        padding-top:4px;
        position: relative;
        /*margin:0;*/
        width:100%;

    }
  .el-header{
    /*background-color: #B3C0D1;*/
    /*color: #333;*/
    /*text-align: center;*/
      margin-left: -15px;
    line-height: 60px;
  }
  .el-header{
    margin-bottom: 5px;
    padding: 0 0;
  }

  .el-aside {
