<template>
  <div class="NavBar">
    <el-input
      suffix-icon="el-icon-search"
      placeholder="           请输入要搜索内容的关键字"
      v-model="input"
      clearable
      @keyup.enter.native="handleSearch"
    >
    </el-input>

    <div class="time">{{ date }}</div>

    <el-link type="primary" @click="handleRegister">还没有账号?</el-link>
    <el-button
      type="info"
      @click="handleLogout"
      round
      class="logout"
      icon="el-icon-s-promotion"
      >注销</el-button
    >
    <el-button type="primary" @click="handleClick" round class="admin">{{
      this.$route.path !== '/home' ? '返回首页' : '后台管理'
    }}</el-button>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
// import eventBus from '@/api/eventBus'
export default {
  name: 'CommonNavBar',
  data() {
    return {
      input: '',
      blogs: [],
      fBlogs: [],
      date: '',
    }
  },
  created() {
    console.log('navbar')
    this.$axios.get('/api/blog/getBlog').then((response) => {
      let res = response.data
      if (res.status) {
        this.$message({
          message: res.msg,
          type: 'warning',
        })
      } else {
        this.blogs = [...res.result]
      }
      this.changeBlogs(this.blogs)
    })
    // eventBus.$emit('init',this.blogs)
  },
  mounted() {
    let _this = this // 声明一个变量指向Vue实例this，保证作用域一致
    this.timer = setInterval(() => {
      let newDate = new Date().toLocaleString()
      let newDate1 = newDate.split('/')
      let newDate2 = newDate1[2].split(' ') || []
      let newTime = newDate2[1].split(':')

      this.date = ` 你好，现在是 ${newDate1[0]} 年 ${newDate1[1]} 月 ${newDate2[0]} 日，${newTime[0]} 时 ${newTime[1]} 分  `
    }, 5000)

    this.timer1 = setInterval(() => {
      //获取到头的第一个字符
      let start = this.date.substring(0, 1)
      //获取到后面的所有字符
      let end = this.date.substring(1)
      //重新拼接得到新的字符串，并赋值给this.message
      this.date = end + start
    }, 200)
  },

  //arr[]    setTimeout(function,1000)
  // function(){
  //     start=arr[0]
  //     arr.splice(0,1)
  //     arr.push(start)
  // }
  //

  beforeDestroy() {
    if (this.timer || this.timer1) {
      clearInterval(this.timer) // 在Vue实例销毁前，清除我们的定时器
      clearInterval(this.timer1) // 在Vue实例销毁前，清除我们的定时器
    }
  },
  computed: {
    ...mapState(['allBlogs', 'filterBlogs']),
  },

  watch: {
    input() {},
  },
  methods: {
    ...mapMutations(['changeFilterBlogs', 'clear', 'changeBlogs']),
    handleRegister() {
      this.$router.push('/register')
    },

    handleSearch() {
      this.clear()
      // console.log(this.filterBlogs);
      this.allBlogs.filter((item) => {
        if (
          item.title.includes(this.input) ||
          item.summary.includes(this.input)
        ) {
          this.fBlogs.push(item)
        }
        this.changeFilterBlogs(this.fBlogs)
      })
      this.$emit('filter', this.fBlogs)
      // console.log(this.fBlogs);
      this.$router.push({
        path: '/searchBlog',
      })
    },
    handleClick() {
      if (this.$route.path !== '/home') {
        this.$router.push('/home')
      } else {
        this.$router.push('/admin/blog')

        this.$axios
          .get('/api/blog/getBlogByUser', {
            params: {
              user_id: sessionStorage.getItem('user_id'),
              state: 1,
            },
          })
          .then((response) => {
            let res = response.data
            if (res.status) {
              this.$message({
                message: '你还没有发布博客~',
              })
            } else {
              let Blogs = [...res.result]
              console.log(Blogs)
              this.changeFilterBlogs(Blogs)
              console.log(this.filterBlogs)
            }
          })
      }
      this.$message.success('正在跳转')
    },
    handleLogout() {
      sessionStorage.clear()
      this.$message.success('注销成功！正在跳转')
      this.$router.replace('/home')
    },
  },
}
</script>

<style scoped>
.NavBar/deep/ {
  width: 98.5%;
  height: 85px;
  margin-left: 20px;
  z-index: 1;
  position: relative;
  border-radius: 10px;
  background-color: #eeeeee;
  display: inline-block;
}
.time {
  color: #cccccc;
  position: absolute;
  top: 10px;
  left: 500px;
}
.el-link {
  position: absolute;
  top: 30px;
  right: 270px;
  line-height: 20px;
}
.el-input {
  width: 350px;
  float: left;
  line-height: 85px;
  margin-left: 50px;
}
.admin {
  position: absolute;
  right: 150px;
  top: 20px;
}
.search {
  float: left;
  margin: 20px 10px;
}

.logout {
  position: absolute;
  right: 43px;
  top: 20px;
}
</style>