<template>
    <div class='TransitionBox'>
        <transition :name='name'>
            <slot></slot>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "TransitionBox",
        props: {
            name:{
                type: String
            }
        }
    }
</script>



<style lang=less>

    .left-leave-active{
        transform:translateX(0px);
        transition:1s;
    }
    .left-leave-to{
        transform:translateX(-100%);
        display:none
    }

    .left-enter-active{
        transform:translateX(100%);
        transition:1s;
    }
    .left-enter-to{
        transform:translateX(0)
    }

    .right-leave-active{
        transform:translateX(0px);
        transition:1s;
    }
    .right-leave-to{
        transform:translateX(100%);
        display:none
    }

    .right-enter-active{
        transform:translateX(-100%);
        transition:1s;
    }
    .right-enter-to{
        transform:translateX(0)
    }

</style>