import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store/index'


Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    component: ()=>import('views/home/Home'),
    meta: { index: 0 }
  },
  {
    path: '/searchBlog',
    component: ()=>import('views/home/Home'),
    meta: { index: -4 }
  },
  {
    path: '/',
    redirect: '/home'
  },
  //登录
  {
    path: '/login',
    name: 'login',
    component: ()=>import('views/login/Login'),
    meta: { index: 1 }
  },
  {
    path: '/register',
    component: ()=>import('views/register/Register'),
    meta: { index: 2 }
  },
  {
    path: '/content',
    component: () => import('views/admin/base/Contents'),
    meta: { index: 9 }
  },
  {
    path: '/editBlog',
    component: () => import('views/admin/base/NewBlog'),
    meta: { index: 10 }
  },
  {
    path: '/admin',
    redirect: '/admin/blog',
    component: ()=>import('views/admin/Admin'),
    meta: {
      requireAuth: true,  // 添加该字段，表示进入这个路由是需要登录的
      index: 3
    },
    children: [
      {
        path: 'blog',
        component: () => import('views/admin/base/Blog'),


        meta: { index: 8 }
      },
      {
        path: 'draft',
        component: () => import('views/admin/base/Blog'),
        meta: { index: 7 }
      },
      {
        path: 'userzone',
        component: () => import('views/admin/base/UserZone'),
        meta: { index: 5 }
      },
      {
        path: 'adminoperation',
        redirect: '/admin/adminoperation/userOperation',
        component: () => import('views/admin/base/AdminOperation'),
        meta: { index: 3 },
        children: [
          {
            path: 'userOperation',
            component: () => import('views/admin/base/UserOperation'),
            meta: {
              type: '/user',
              routerObject: '/deleteUser'
            }
          },
          {
            path: 'blogOperation',
            component: () => import('views/admin/base/UserOperation'),
            meta: {
              type: '/blog',
              routerObject: '/deleteBlog'
            }
          },
        ]
      },
      {
        path: 'comment',
        component: () => import('views/admin/base/Comment'),
        meta: { index: 6 }
      },
      {
        path: 'newBlog',
        component: () => import('views/admin/base/NewBlog'),
        // component: (resolve) => require(["views/admin/base/NewBlog"], resolve),
        meta: { index: 11 }
      }
    ]
  },
  {
    path: '/about',
    component: ()=>import('views/about/About'),
    meta: { index: -3 }
  },
  {
    path: '/message',
    component: ()=>import('views/message/Message'),
    meta: { index: -2 }
  },
  {
    path: '/announcement',
    component: ()=>import('views/announcement/Announcement'),
    meta: { index: -1 }
  }
]



const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to,from,next)=>{
  // if (!store.state.token) {
  //   store.commit("saveToken",window.localStorage.Token)
  // }
  if (to.path === '/admin/blog'&&!sessionStorage.getItem('username')){
    next('/login')
  } else {
    next()
  }

})
export default router
