<template>
    <div>

        <quill-editor v-model="currentValue" ref="myQuillEditor" :options="editorOption" class="ql-editor-class n-editor"></quill-editor>
    </div>

</template>
<script>
    import 'iview'
    import { addQuillTitle } from '@/api/quill-title.js'
    import { quillEditor } from "vue-quill-editor";
    import "quill/dist/quill.snow.css";
    import "quill/dist/quill.bubble.css";
    import "quill/dist/quill.core.css";
    export default {
        name: "Editor",
        components: {
            quillEditor
        },
        mounted(){
            // console.log(this.$refs.myQuillEditor.quill);
            addQuillTitle();
        },
        methods:{

        },
        props: {
            value: {
                type: String
            },


            editorOption: {               //符文本编辑器的配置
                modules: {
                    toolbar: {

                        handlers: {
                            'image': function (value) {
                                if (value) {
                                    // 给个点击触发Element-ui，input框选择图片文件
                                    document.querySelector('#avatar-uploader').click()
                                } else {
                                    this.quill.format('image', false);
                                }
                            }
                        }
                    }
                }
            }
        },
        data() {
            return {
                // uploadImgUrl: "/api/blog/img",
                currentValue: this.value
            };
        },
        watch: {
            value(v) {
                this.currentValue = v;
            },
            currentValue(v) {
                this.$emit("input", v);
            }
        }
    };
</script>
<style lang=scss>

    .ql-editor-class {
        height: auto;
        min-height: 320px;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        line-height: 1.42;
        height: 100%;
        outline: none;
        padding: 0 !important;
        tab-size: 4;
        -moz-tab-size: 4;
        text-align: left;
        word-wrap: break-word;
    }

    .n-editor .ql-editor {
        height: auto;
        min-height: 320px;
    }
    /*    height: auto;*/
    /*    min-height: 320px;*/
    /*}*/
</style>